import api from '../api';
import type { Review, CreateReviewRequest } from '../types';

export const ratingService = {
  // Get reviews for a product
  async getProductReviews(productId: string): Promise<Review[]> {
    const response = await api.get<Review[]>(`/api/ratings/product/${productId}`);
    return response.data;
  },

  // Create a review
  async createReview(data: CreateReviewRequest): Promise<Review> {
    const response = await api.post<Review>('/api/ratings', data);
    return response.data;
  },

  // Update a review
  async updateReview(reviewId: string, data: Partial<CreateReviewRequest>): Promise<Review> {
    const response = await api.put<Review>(`/api/ratings/${reviewId}`, data);
    return response.data;
  },

  // Delete a review
  async deleteReview(reviewId: string): Promise<void> {
    await api.delete(`/api/ratings/${reviewId}`);
  },
};
