import api from '../api';
import type { Product } from '../types';

// Transform API product to frontend Product format
const transformProduct = (apiProduct: any): Product => {
  // Extract features from attributes
  const features: string[] = []
  if (apiProduct.attributes) {
    Object.entries(apiProduct.attributes).forEach(([key, value]) => {
      if (typeof value === 'string' && value.length > 0) {
        features.push(value)
      } else if (typeof value === 'boolean' && value) {
        features.push(key)
      }
    })
  }

  // Handle image path - if it's a relative path, use placeholder for now
  let imagePath = apiProduct.images?.[0] || apiProduct.image || '/placeholder.svg'
  // If image path doesn't start with http, use placeholder
  if (!imagePath.startsWith('http') && !imagePath.startsWith('/')) {
    imagePath = '/placeholder.svg'
  } else if (imagePath.startsWith('/images/')) {
    // For now, use placeholder since we don't have actual image files
    imagePath = '/placeholder.svg'
  }

  return {
    id: apiProduct._id || apiProduct.id,
    name: apiProduct.name,
    description: apiProduct.description,
    price: apiProduct.price,
    originalPrice: apiProduct.originalPrice,
    image: imagePath,
    category: apiProduct.category,
    rating: apiProduct.rating || 4.5, // Default rating
    reviewCount: apiProduct.reviewCount || 0,
    inStock: (apiProduct.stock || 0) > 0,
    features: features.length > 0 ? features : ['Ch·∫•t l∆∞·ª£ng cao', 'B·∫£o h√†nh ch√≠nh h√£ng'],
  };
};

export const productService = {
  // Get all products
  async getProducts(params?: {
    category?: string;
    search?: string;
    minPrice?: number;
    maxPrice?: number;
    sortBy?: string;
    page?: number;
    limit?: number;
  }): Promise<Product[]> {
    const response = await api.get<any>('/api/products', { params });
    // API returns { products: [], total: number, skip: number, limit: number }
    const products = response.data.products || response.data;
    return Array.isArray(products) ? products.map(transformProduct) : [];
  },

  // Get product by ID
  async getProductById(id: string): Promise<Product> {
    const response = await api.get<any>(`/api/products/${id}`);
    return transformProduct(response.data);
  },

  // Search products
  async searchProducts(query: string): Promise<Product[]> {
    const response = await api.get<any>('/api/products', {
      params: { search: query },
    });
    const products = response.data.products || response.data;
    return Array.isArray(products) ? products.map(transformProduct) : [];
  },
};
